@import url(https://fonts.googleapis.com/css?family=Borel);
@import url(https://fonts.googleapis.com/css?family=Caprasimo);
@import url(https://fonts.googleapis.com/css?family=Montserrat);

.App {
     text-align: center;
}

.App-logo {
     height: 40vmin;
     pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
     .App-logo {
          animation: App-logo-spin infinite 20s linear;
     }
}

.App-header {
     background-color: #282c34;
     min-height: 100vh;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     font-size: calc(10px + 2vmin);
     color: white;
}

.App-link {
     color: #61dafb;
}

@keyframes App-logo-spin {
     from {
          transform: rotate(0deg);
     }

     to {
          transform: rotate(360deg);
     }
}

a {
     text-decoration: none;
     color: rgb(88, 89, 91)
}

.body {
     min-height: 100%;
}

.client-portal {
     margin: 1.5%;
}

.client-portal__h2 {
     margin: 3px 0;
}

.footer__spacer {
     height: 6.5rem;
}

.header__spacer {
     height: 8.15rem;
}

.phone-form {
     margin: 1.5%;
     padding-bottom: 2%;
}